.message-wrapper{
  display: flex;
  margin-top: 30px;

  &.start{
    justify-content: flex-start;
  }

  &.end{
    flex-direction: row-reverse;
  }

  .message{
    display: flex;
    flex-direction: column;
    padding: 5px 20px;
    background: #eaeaee;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    max-width: 60%;
    position: relative;

    h3 {
      margin: 0;
      font-size: 15px;

      font-weight: 600;
    }

    p{
      max-width: 100%;
      margin: 0;

      overflow-wrap: break-word;
      white-space: pre-wrap;

      font-size: 15px;
      line-height: 1.3;
    }

    &.end{
      background: #daf1db;
      border-top-left-radius: 15px;
      margin-right: 5px;
    }

    &.start{
      border-top-right-radius: 15px;
      margin-left: 5px;
    }
  }
}

@media (max-width: 600px) {
  .MuiPaper-root .messages .message-wrapper .message
  {
    max-width: 80%;
  }
}