.App{
  height: 100vh;
  padding: 30px 0;

  background: #d9dce3;

  .init-loading {
    width: 100vw;
    height: 100vh;
  }
}
.MuiContainer-root{
  margin: 0 auto !important;
  padding: 0 !important;
  display: flex !important;
  justify-content: center;
}

::-webkit-scrollbar {
  width: 7px;
  background-color: #f9f9fd;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: #CECECE;
  border-radius: 10px;
}
